* {
  margin: 0;
  box-sizing: border-box;
}
* button {
  cursor: pointer;
}
.container1 {
  position: relative;
  width: 100%;
  height: 800px;
  padding-top: 20px;
}
.container1 ::after {
  z-index: -2;
  content: '';
  position: absolute;
  width: 100%;
  height: 800px;
  padding-top: 20px;
  background-image: url('https://assets.nflxext.com/ffe/siteui/vlv3/69b9cce3-4fdc-4ed0-8825-9b24c97c3993/9a474f01-24f3-452b-b585-676813ac2fba/KR-ko-20201005-popsignuptwoweeks-perspective_alpha_website_small.jpg');
  opacity: 0.1;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 56px;
  padding: 0 56px;
}
.log-box img {
  zoom: 0.3;
}
.log-in-box button {
  color: white;
  background-color: #f42b13;
  line-height: normal;
  padding: 7px 17px;
  font-weight: 400;
  font-size: 1rem;
  border-radius: 3px;
  outline: 0;
  border: 0;
}
.main-card {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: white;
}
.mant-card {
  width: 950px;
  height: 416px;
}
.main-card h1 {
  margin: 0 75px;
  font-size: 5.5rem;
}

.sub-title h2 {
  display: block;
  margin: 16px 75px;
  font-size: 25px;
}

.sub-title h3 {
  display: block;
  margin: 16px 75px;
  font-size: 19.2px;
}
.main-email {
  padding-top: 13.6px;
}
.main-email input {
  width: 500px;
  height: 70px;
  font-size: 2rem;
}
.main-email button {
  font-size: 30px;
  color: white;
  padding: 0;
  width: 270px;
  height: 70px;
  background-color: #f42b13;
  border: 0;
  outline: 0;
}
.input-box {
  display: flex;
  justify-content: center;
}
.input-box h3 {
  display: block;
  margin: 14.4px 0px 4.8px;
  font-size: 19.2px;
}
.card {
  display: flex;
  justify-content: center;
  margin-top: 30px;
  height: 473.92px;
  padding: 70px 45px;
  background-color: black;
}
.contants-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 1100px;
  height: 325px;
  color: white;
}
.main-txt {
  width: 547px;
}
.img-box {
  position: relative;
}
.img-box video {
  position: absolute;
  width: 387px;
  height: 216px;
  left: 13%;
  top: 21%;
}
.img-box img {
  width: 530px;
  height: 400px;
}
@media screen and (max-width: 950px) {
  .main-card {
    zoom: 0.7;
  }
  .input-box input,
  .input-box button {
    zoom: 0.7;
  }
  .contants-box {
    flex-direction: column;
    font-size: 12px;
  }
}
.sub-header {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 56px;
  padding: 0 56px;
}
.sub-header img {
  zoom: 0.7;
}
.title-box {
  margin-top: 5%;
}

.conts-box {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 100vh;
  padding: 0 20px;
}
.mini-title {
  margin-top: 5%;
  margin-left: 3%;
  display: flex;
  justify-content: start;
  font-weight: 700;
  font-size: 20px;
}
.movie-poseter {
  position: relative;
  margin-left: 20px;
  margin-top: 2%;
  width: 350px;
  height: 250px;

  font-size: 15px;
}
.movie-poseter :hover {
  cursor: pointer;
}
.movie-poseter img {
  position: relative;
  width: 350px;
  height: 250px;
}
.title-info {
  width: 350px;
  background-color: rgba(000, 000, 000, 0.5);
  position: absolute;
  font-weight: 700;
  padding: 2.5px 2.5px;
  left: 0;
  bottom: 1%;
  transition: all ease 5s 5s;
}
.detail-title {
  margin-top: 5%;
  color: whitesmoke;
  width: 100%;
}
.detail-title h1 {
  width: 250px;
  margin-left: 10%;
}

.detail-main {
  position: absolute;
  width: 100%;
  height: 800px;
  overflow: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 150px;
  margin-top: 3%;
  color: white;
  width: 100%;
  top: 30%;
  left: 15%;
}
.detail-main::-webkit-scrollbar {
}
.today-todo::-webkit-scrollbar-thumb {
  background-color: rgba(255, 255, 255, 0.5);
}
.back {
  margin-top: 5%;
  position: relative;
  width: 1500px;
  animation: fadein 3s;
  -moz-animation: fadein 3s; /* Firefox */
  -webkit-animation: fadein 3s; /* Safari and Chrome */
  -o-animation: fadein 3s;
}
.imgbox {
  position: absolute;
  width: 700px;
  height: 800px;
  margin-top: 2%;
  margin-right: 35%;
  zoom: 0.7;
  top: 40%;
  left: 15%;
  animation: fadein 3s;
  -moz-animation: fadein 3s; /* Firefox */
  -webkit-animation: fadein 3s; /* Safari and Chrome */
  -o-animation: fadein 3s;
}
.info-box {
  background-color: rgba(225, 225, 255, 0);
  margin-top: 40px;
  width: 50%;
  color: white;
  animation: fadein 3s;
  -moz-animation: fadein 3s; /* Firefox */
  -webkit-animation: fadein 3s; /* Safari and Chrome */
  -o-animation: fadein 3s;
}

.contns-text-box {
  padding: 20px;
  font-weight: 700;
  cursor: pointer;
  background-color: rgba(000, 000, 000, 0.5);
  padding: 10px;
  animation: fadein 3s;
  -moz-animation: fadein 3s; /* Firefox */
  -webkit-animation: fadein 3s; /* Safari and Chrome */
  -o-animation: fadein 3s; /* Opera */
}
@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-moz-keyframes fadein {
  /* Firefox */
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-webkit-keyframes fadein {
  /* Safari and Chrome */
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-o-keyframes fadein {
  /* Opera */
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
